






































































































































































































































#QualityAssessment {
  padding: 10px;
  width: 100%;
  height: 100%;
  .searchesBox {
    width: 49%;
    overflow: hidden;
    .ivu-form-item {
      margin-bottom: 0;
      float: left;
    }
    margin-bottom: 10px;
  }
  .assessmentBox {
    width: 100%;
    height: calc(100% - 42px);
    overflow: hidden;
    .left {
      float: left;
      width: 49%;
      margin-left: 5px;
      height: calc(100% - 5px);
    }
    .right {
      position: relative;
      float: right;
      width: 49%;
      height: calc(100% - 5px);
      margin-right: 5px;
    }
    .exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      margin-right: 10px;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
    .queryBtns {
      float: right;
      margin-top: 6px;
    }
  }
}
